<template>
  <div class="employee-page">
    <h4 class="page-head">Manage Employees</h4>
    <div class="emp-btn-row">
      <button @click="toggleFilter" class="filter-btn">
        <i class="fa-solid fa-sort"></i> Filter
      </button>
      <button @click="showAddUserForm" class="adduser-btn">
        <i class="fa-solid fa-plus"></i> Add User
      </button>
    </div>

    <div v-if="showAddUser || editingUser">
      <!-- Add/Edit User Form -->
      <form @submit.prevent="addOrUpdateUser">
        <label for="userName">Name:</label>
        <input v-model="userForm.name" id="userName" required>

        <label for="userDesignation">Designation:</label>
        <input v-model="userForm.designation" id="userDesignation" required>

        <label for="userDesignation">Status</label>
        <input v-model="userForm.status" id="userStatus" required>

        <label for="userEid">Employee ID</label>
        <input v-model="userForm.eid" id="userEid" required>

        <button type="submit">{{ editingUser ? 'Update User' : 'Save User' }}</button>
      </form>
    </div>

    <!-- User List -->
    <div class="row userlist-table">
      <table>
        <thead>
          <tr>
            <th>User Name</th>
            <th>User Designation</th>
            <th>Employee ID</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in filteredUsers" :key="user.id">
            <td>{{ user.name }}</td>
            <td>{{ user.designation }}</td>
            <td>{{ user.eid }}</td>
            <td>{{ user.status }}</td>
            <td class="action-btn">
              <button @click="editUser(user)" class="edit-btn"><i class="fa-solid fa-pen"></i></button>
              <button @click="deleteUser(user.id)" class="trash-btn"><i class="fa-solid fa-trash"></i></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'EmployeePage',
  data() {
    return {
      showFilter: false,
      showAddUser: false,
      users: [
        { id: 1, name: 'Pradeep', designation: 'Developer', status: 'Active', eid: '2201', },
        { id: 2, name: 'Surya', designation: 'Designer', status: 'Inactive', eid: '2202', },
      ],
      editingUser: null,
      userForm: {
        name: '',
        designation: '',
        eid: '',
        status: '',
      },
    };
  },
  computed: {
    filteredUsers() {
      return this.users;
    },
  },
  methods: {
    async toggleFilter() {
      this.showFilter = !this.showFilter;
    },
    showAddUserForm() {
      this.showAddUser = true;
      this.editingUser = null;
      this.resetUserForm();
    },

    async addOrUpdateUser() {
      if (this.editingUser) {
        // Update user
        try {
          const response = await axios.put(`http://192.168.1.8/attendancenew/public/api/putemployee/${this.editingUser.id}`, this.userForm);
          const updatedUser = response.data;
          const index = this.users.findIndex((user) => user.id === this.editingUser.id);
          if (index !== -1) {
            this.$set(this.users, index, updatedUser);
          }
        } catch (error) {
          console.error('Error updating user:', error);
        }
      } else {
        // Add new user
        try {
          const response = await axios.post('http://192.168.1.8/attendancenew/public/api/addemployee', this.userForm);
          const newUser = response.data;
          this.users.push(newUser);
        } catch (error) {
          console.error('Error adding user:', error);
        }
      }

      // Reset form and hide
      this.resetUserForm();
      this.showAddUser = false;
    },

    async editUser(user) {
      this.showAddUser = true;
      this.editingUser = user;
      this.userForm = { ...user };
    },
    async deleteUser(userId) {
      try {
        await axios.delete(`http://192.168.1.8/attendancenew/public/api/deleteemployee/${userId}`);
        this.users = this.users.filter((user) => user.id !== userId);
      } catch (error) {
        console.error('Error deleting user:', error);
      }
    },
    resetUserForm() {
      this.editingUser = null;
      this.userForm = {
        name: '',
        designation: '',
        eid: '',
        status: '',
      };
    },
  },
  async mounted() {
    try {
      const response = await axios.get('http://192.168.1.8/attendancenew/public/api/getemployee');
      this.users = response.data;
    } catch (error) {
      console.error('Error fetching users:', error);
    }
  },
};
</script>

<style scoped>
/* Your styles here */
</style>

  
<style scoped>
.employee-page {
  margin-top: 1% !important;
}

.page-head {
  font-size: 32px;
  color: #012970;

}

.emp-btn-row {
  margin: 30px 0px;
}

.emp-btn-row button {
  width: fit-content;
  padding: 8px 25px;
  border-radius: 5px;
  font-size: 18px;
}

.emp-btn-row button:hover {
  box-shadow: 0px 0px 6px #b4b2b2;
}

.filter-btn {
  margin-right: 15px;
  margin-left: 35px;
  background-color: white;
  border: 0.5px solid rgb(204, 199, 199);
}

.adduser-btn {
  margin-right: 20px;
  margin-left: 5px;
  background-color: rgb(0, 119, 255);
  border: 0.5px solid rgb(204, 199, 199);
  color: white;
}

.userlist-table {
  margin-left: 35px;
  background-color: white;
  border: 1px solid #cfcbcb;
  border-radius: 10px;
  max-width: 1290px;
}

.userlist-table thead tr {
  background-color: #f9fafb;
  color: #8a92a6;
  border-radius: 10px;

}

.userlist-table thead tr th,
td {
  padding: 15px 5px;
  padding-left: 20px;
  border-radius: 10px;
}

.userlist-table tr td {
  border-top: 0.5px solid rgb(238, 232, 232);
}

.action-btn button {
  border: none;
  background-color: transparent;
}

.edit-btn:hover {
  color: green;
}

.trash-btn:hover {
  color: red;
}
</style>