<template>
    <div class="container">
      <div class="card login-card">
        <div class="row">
          <div class="col-sm-7 login-left-col">
            <!-- Your left column content -->
          </div>
          <div class="col-sm-5 login-right-col">
            <div class="col-sm-7">
              <img :src="companyLogo" class="img-fluid img-responsive company-logo">
            </div>
            <div class="col-sm-7">
              <h5 style="text-align: left; margin-left: 10px;">Login-Here</h5>
            </div>
            <br>
            <div class="col-sm-8 login-form">
              <input v-model="username" type="text" placeholder="Username" required>
              <br>
              <input v-model="password" type="password" placeholder="Password" required>
              <br>
              <button class="login-btn" @click="submitForm">Login</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    name: 'LoginPage',
    data() {
      return {
        companyLogo: './assets/kavisoftek.png',
        username: '',
        password: '',
      };
    },
    methods: {
      async submitForm() {
        try {
          if (!this.username || !this.password) {
            console.error('Please enter both username and password');
            return;
          }
  
          const response = await axios.post('http://192.168.1.8/attendancenew/public/api/attendance-store', {
            username: this.username,
            password: this.password,
          });
  
          if (response && response.data) {
            console.log('Authentication Successful:', response.data);
            this.$router.push('/admin-panel');
          } else {
            console.error('Invalid response from the server:', response);
          }
        } catch (error) {
          console.error('Authentication Failed:', error.response ? error.response.data : error.message);
        }
      },
    }
  }
  </script>
  
<style scoped>
.login-left-col {
    background-image: linear-gradient(to bottom, #0066b3, #02298a, #021b79);
    border-radius: 5px 0px 0px 5px;
}

.login-form {
    float: left;
    text-align: left;
}

.login-form input {
    padding: 8px;
    width: 100%;
    border-radius: 20px;
    border: 0.5px solid rgb(218, 211, 211);
    margin: 12px 0px;
}

.login-btn {
    width: 100%;
    border-radius: 20px;
    padding: 8px;
    background-color: #0066b3;
    border: 0.5px solid transparent;
    color: white;
    margin-top: 14px;
}

.login-right-col {
    padding: 140px 60px;
}

.login-card {
    margin-top: 9%;
}

.login-left-text {
    text-align: left;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Orbitron', sans-serif;
    font-family: 'Poppins', sans-serif;
    font-family: 'Raleway', sans-serif;
}
</style>
  